@import url('https://fonts.googleapis.com/css2?family=Inter:wght@500;700;900&display=swap');
*{
  -webkit-tap-highlight-color: transparent;
}
body {
  margin: 0;
  padding: 0;
  text-decoration: 0;
  font-family: "Inter";
}
div#root {
  display: flex;
  justify-content: center;
  overflow: hidden;
}
.title{
  font-weight: 900;
  font-size: 24px;
  line-height: 39px;
}
a {
  -webkit-tap-highlight-color: transparent;
  text-decoration: none;
}
