
.contactBox {
    display: flex;
    align-items: center;
    background: royalblue;
    border-radius: 15px;
    padding: 15px;
    height: 40px;
    width: 100%;
    max-width: 383px;
}
.contactBox > h1 {
    color: white;
    font-weight: 900;
    font-size: 98px;
    position: absolute;
    margin-left: -17px;
    margin-top: 66px;
}
.contactBox > span {
    margin-left: 150px;
    color: white;
    font-weight: 500;
    font-size: 30px;
    border-bottom: 4px solid white;
}