.gameHeader {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 10px;
    padding-top: 10px;
    position: fixed;
    width: 100%;
    transition: .2s;
    height: 46px;
    max-width: 768px;
    z-index: 2;
}
.headline {
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: white;
    transition: .3s;
    
}
.tagline {
    font-style: italic;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: white;
    transition: .2s;
    opacity: 1;
}
.block {
    background: white;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
    border-radius: 30px;
    padding: 15px;
    margin-bottom: 10px;
}
.btn {
    height: 50px;
    display: grid;
    justify-content: center;
    align-items: center;
    background: black;
    color: white;
    border-radius: 20px;
    margin-top: 15px;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
    font-size: 20px;
    font-weight: 900;
    font-size: 20px;
    line-height: 24px;
    transition: .2s;
    cursor: pointer;
}
.narrowBlock {
    display: flex;
    background: white;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
    padding: 22px;
    border-radius: 30px;
    margin-top: 10px;
}
.numberBlock {
    display: grid;
    align-items: center;
}
.numberСircle {
    background: #EFEFEF;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    display: grid;
    justify-content: center;
    align-items: center;
}
.numberСircle > span {
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
}
.narrowBlockDesc {
    margin-left: 20px;
}
.descTitle > span {
    font-weight: 900;
    font-size: 20px;
    line-height: 29px;
    padding-bottom: 1px;
}
.descText {
    font-weight: bold;
    font-size: 14px;
    line-height: 17px;
    color: #acacac;
}
.goBackBtn {
    width: 30px;
    height: 30px;
    position: absolute;
    display: grid;
    top: 20px;
    align-content: center;
    justify-content: center;
    transition: .2s;
    cursor: pointer;
    left: 15px;
    border-radius: 10px;
}
.goBackBtn > svg {
    fill: white;
}
.goBackBtn:hover {
    background: #ffffff3b;
    border-radius: 10px;
}
.goBackBtn:active {
    background: #ffffff85;
}
.accentBtn:hover {
    filter: brightness(0.8);
}
.mediaContainer{
    position: absolute;
    background: white;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    transition: .3s cubic-bezier(0.28, 0.69, 0.58, 1.12);
    z-index: 3;
}
.mediaBlock {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 10px 15px 15px 15px;
}
.mediaBlockImg {
    width: 110px;
    height: 110px;
    overflow: hidden;
    border-radius: 10px;
    margin: 2.5px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}
.closeBtnBox {
    display: flex;
    margin-top: 10px;
    padding: 0 17.5px 0 17.5px;
}
.closeBtn {
    font-size: 14px;
    font-family: Inter;
    font-weight: 900;
    padding: 8px 20px 8px 15px;
    background: black;
    color: white;
    border-radius: 10px;
    display: flex;
    align-items: center;
    cursor: pointer;
}
.background {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    display: flex;
    position: fixed;
    justify-content: center;
    z-index: 1;
    align-items: center;
    background: #000000ad;
    transition: .2s;
    z-index: 4;
}
.headerLayer {
    width: calc(100% - 30px);
    padding: 0 15px 0 15px;
    position: fixed;
    margin-top: 66px;
    z-index: 0;
    max-width: 738px;
}
.headerDesc{
    color: black;
    text-align: -webkit-center;
    background: white;
    font-weight: bold;
    border-radius: 10px;
    padding: 10px;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
    position: relative;
    z-index: 1;
}
.infoBlock {
    font-weight: 600;
    font-size: 17px;
    display: flex;
    padding: 15px 10px;
    justify-content: space-between;
}
.infoContainer {
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    padding: 10px;
}
.transformedHeader > .headline {
    color: black;
    margin-top: 9.5px;
    font-size: 22px;
}
.transformedHeader {
    background: #ffffff !important;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.15);
}
.transformedHeader > .tagline {
    display: none;
    opacity: 0;
}
.transformedHeader > div > .goBackBtn > svg {
    fill: black;
}