.app{
    width: 100%;
    margin-bottom: 45px;
    max-width: 768px;
    display: inline-flex;
}
.navBarBlock {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    text-align: -webkit-center;
    z-index: 2;
}
.navBar {
    display: flex;
    height: 45px;
    justify-content: space-around;
    background: white;
    max-width: 768px;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.15);
}
.linkIcon {
    width: 24px;
    height: 24px;
}
span.linkText {
    font-weight: 700;
    font-size: 13px;
    color: #C5C5C5;
    line-height: 16px;
    transition: .2s;
}
.linkBlock {
    display: grid;
    justify-items: center;
    align-content: center;
}
svg {
    height: 24px;
    fill: #c5c5c5;
    width: 24px;
    transition: .2s;
}
.navLink{
    text-decoration: none;
    align-self: center;
}
.navLinkActive > .linkBlock > span {
    color: black !important;
}
.navLinkActive > .linkBlock > .linkIcon > svg {
    fill: black;
}
.gameBar > div {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}
.gameBar > div > a {
    display: grid;
    max-width: 369px;
    width: 100%;
}
.paddingBox {
    padding: 10px 15px 15px 15px;
    background: white;
    z-index: 1;
    position: relative;
}
