.imgBlock {
    width: 340px;
    height: 300px;
    background: white;
    border-radius: 30px;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    display: flex;
    justify-content: center;
    border: 5px solid white;
    margin-right: 10px;
    align-items: center;
}
img {
    height: 100%;
}
.imgSlider {
    display: inline-flex;
}
.imgBox {
    border-radius: 30px;
}
img.width {
    width: 100%;
    height: fit-content;
}
.imgBtnBlock {
    position: absolute;
    display: flex;
    width: 100%;
    height: 300px;
    left: 0;
    margin-top: 5px;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
}
.imgBtn {
    background: white;
    height: 40px;
    width: 40px;
    display: grid;
    justify-content: center;
    align-content: center;
    border-radius: 10px;
    margin: 10px;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.15);
    border: 3px solid #c5c5c5;
    cursor: pointer;
    transition: .2s;
}
.imgBtn:hover {
    border: 3px solid #848484;
}
.imgBtn > svg {
    height: 30px;
    width: 30px;
}
.imgBtn:hover > svg {
    fill:#848484;
}