.gameBlock {
    background: white;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
    border-radius: 30px;
    height: 100px;
    display: flex;
    padding: 10px;
    margin: 15px 5px 0px 5px;
}
.gameImgBox {
    width: 110px;
}
.img {
    display: flex;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    justify-content: center;
    align-items: center;
    transition: .2s;
}
.img> img{
    height: 120px;
    position: absolute;
    margin-top: -30px;
}
.gameDescBox {
    display: flex;
    margin-left: 15px;
    flex-direction: column;
}
span.gameTitle {
    font-weight: 900;
    font-size: 16px;
    line-height: 19px;
    color: black;
}
span.gameShortDesc {
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;
    color: #C5C5C5;
    width: 188px;
}
.gameBlock:hover > .gameImgBox > .img {
    margin-top: -8px;
}